import React from "react";
import { StaticQuery, graphql } from "gatsby";
import { ThemeProvider, createGlobalStyle } from "styled-components";

const Root = ({ children }) => {
  return (
    <StaticQuery
      query={graphql`
        query {
          site {
            siteMetadata {
              theme {
                colors {
                  primary
                  primaryAccent
                  secondary
                  background
                  textColor
                }
              }
            }
          }
        }
      `}
      render={data => {
        return (
          <ThemeProvider theme={data.site.siteMetadata.theme}>
            <>
              <GlobalStyle />
              {children}
            </>
          </ThemeProvider>
        );
      }}
    />
  );
};

export default Root;

const GlobalStyle = createGlobalStyle`
  @media print {
    body {
      color: black;
      background: white;
    }
  }
`;
